.gpt3_header {
    display: flex;
    flex-direction: row;
}

.gpt3_header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;

}

.gpt3_header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3_header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);

    margin-top: 1.5rem;


}

.gpt3_header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
    flex-direction: row;
}

.gpt3_header-content_input input {
    flex:2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1 rem;
    outline: none;
    color: #fff;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;


}

.gpt3_header-content_input button {
    flex:0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: #ff4820;
    border: 2px solid #ff4820;
    padding: 0 1 rem;
    outline: none;
    color: #fff;
    cursor: pointer;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;


}

.gpt3_header-content_people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;

    margin-top: 2rem;
}

.gpt3_header-content_people img {
    width: 181.79px;
    height: 38px;
}

.gpt3_header-content_people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.gpt3_header-image {
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3_header-image img{

    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3_header {
        flex-direction: column;

    }

    .gpt3_header-content{
        margin: 0 0 3rem;
    }
    
}

@media screen and (max-width: 650px) {

    .gpt3_header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3_header-content p {
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3_header-content_people {
        flex-direction: column;
    }

    .gpt3_header-content_people p {
        margin: 0;
    }

    .gpt3_header-content_input input,
    .gpt3_header-content_input button {
        font-size: 16px;
        line-height: 24px;
    }
    
}

@media screen and (max-width: 490px) {

    .gpt3_header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3_header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3_header-content_input input,
    .gpt3_header-content_input button {
        font-size: 12px;
        line-height: 16px;
    }
    
}